.filters{background-color:#040404;min-height:100vh;height:fit-content;width:100vw}.filters_content_container{display:flex;flex-direction:column;align-items:center;padding-bottom:0;direction:rtl;width:85%;height:fit-content;min-height:100vh;background-image:url("../assests/pxfuel.jpg");background-position:bottom right;background-repeat:repeat;background-size:contain;margin:0 auto;max-width:2000px}.filters_content_container_backdrop{width:100%;height:inherit;min-height:100vh;background:linear-gradient(180deg, rgba(27, 27, 27, 0.356) 0%, rgb(27, 27, 27) 18%);padding-top:5rem;backdrop-filter:blur(5px)}.filters_title{align-self:flex-start;color:#fff;padding:0 1rem 1rem 1rem}.filters_content{height:fit-content;width:100%;display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr 1fr;aspect-ratio:2;max-height:20rem;gap:10px;padding:0 1rem}.filters_options_wrapper{width:60%;height:3rem;margin-top:5rem;margin-bottom:4rem;margin-left:auto;margin-right:auto;display:flex;align-items:center;justify-content:space-evenly}.filters_seq_container,.filters_filter_container{display:flex;flex-direction:column;align-items:center;justify-content:space-around;height:fit-content;width:15rem;background:#141414;transform:scaleY(0);transform-origin:top;transition:all .2s}.filters_seq_container label,.filters_filter_container label{color:#fff;white-space:nowrap;display:flex;align-items:center;flex-direction:row-reverse}.filters_filter_col_con{display:flex;align-items:center;flex-direction:column}.filters_filter_container{flex-direction:row}.filters_input_show-state{width:15rem;border:1px solid #fff;border-radius:5px;background-color:rgba(26,26,26,.757);padding:.5rem 0;margin:.8rem 2rem;margin-bottom:0;cursor:pointer !important;font-size:small !important}.filters_input_show-state:focus{border:none;outline:none}.filters_sort_item{height:fit-content;width:inherit;display:flex;align-items:center;justify-content:flex-start;padding:.8rem 1.5rem;font-size:small;transition:all .2s;border-radius:5px}.filters_sort_item:hover{background-color:rgba(42,42,42,.757)}.filters_sort_checkbox{width:1rem !important;height:1rem !important;border-radius:5px;border:2px solid #fff;transition:all .2s ease-in-out;position:relative;margin-left:1rem;cursor:pointer}.filters_sort_checkbox::after{content:"✓";color:#fff;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);opacity:0;transition:all .2s ease-in-out}.filters_sort_label{cursor:pointer}.filters_input_checkbox{display:none}.filters_input_checkbox:checked~.filters_sort_label *{border-color:#fdc13b}.filters_input_checkbox:checked~.filters_sort_label .filters_sort_checkbox::after{opacity:1;color:#fdc13b}@media only screen and (max-width: 600px){.filters_content{grid-template-columns:1fr}.filters_options_wrapper{width:100%}.filters_content_container{width:100%}.filters_filter_col_con{width:50%}}@media only screen and (min-width: 600px){.filters_content{grid-template-columns:1fr 1fr 1fr}}@media only screen and (min-width: 768px){.filters_content{grid-template-columns:1fr 1fr 1fr 1fr}}@media only screen and (min-width: 992px){.filters_content{grid-template-columns:1fr 1fr 1fr 1fr 1fr}}@media only screen and (min-width: 1200px){.filters_content{grid-template-columns:1fr 1fr 1fr 1fr 1fr 1fr}}